let THREE = window.THREE = require('three');

const path = "texture/skyBox/";
const format = '.jpg';

const textures = {
    px: path + 'px'+ format,
    nx: path + 'nx'+ format,
    py: path + 'py' + format,
    ny: path + 'ny' + format,
    pz: path + 'pz' + format,
    nz: path + 'nz' + format
};

export default class SkyBox {
    #scene = null;
    constructor(scene, {px, nx, py, ny, pz, nz}=textures) {
        this.#scene = scene;

        let urls = genTexture({px, nx, py, ny, pz, nz});

        this.#scene.background = new THREE.CubeTextureLoader().load( urls );
    }

    updateTexture({px, nx, py, ny, pz, nz}){
        let urls = genTexture({px, nx, py, ny, pz, nz});
        this.#scene.background = new THREE.CubeTextureLoader().load( urls );
    }

}

function genTexture({px, nx, py, ny, pz, nz}) {
    let _params = Object.assign({}, textures, {px, nx, py, ny, pz, nz});

    return  [
        _params.px, _params.nx,
        _params.py, _params.ny,
        _params.pz, _params.nz
    ];
}
