/**
 * Author: 姚应龙
 * Create Time: 2019-05-24
 * Description: 当前场景的视角
 */

let d3 = Object.assign({},
    require('d3-scale'), require('d3-timer'), require('d3-array'));

//视角改变动画的执行时间
let duration_changePerspective = 1000;

let defaultFlyToOpts = {
    duration: 1000
};


export default class Viewpoint {
    //region 视角
    static #PERSPECTIVE_BOTTOM = 'BOTTOM';
    static #PERSPECTIVE_TOP = 'TOP';
    static #PERSPECTIVE_LEFT = 'LEFT';
    static #PERSPECTIVE_RIGHT = 'RIGHT';
    static #PERSPECTIVE_FRONT = 'FRONT';
    static #PERSPECTIVE_AFTER = 'AFTER';
    static #PERSPECTIVE_INIT = 'INIT';
    //endregion

    #scene = null;
    boundingBox = null;

    constructor(scene) {
        this.#scene = scene;
    }

    /**
     * 改变视角
     * 根据模型外界多变形的外接圆来旋转模型
     * @param orientation 视角方向
     */
    changeViewpoint(orientation){
        let boundingSphere = this.#scene.boundingBox.getBoundingSphere();
        let center = boundingSphere.center;
        const deltaHeight = boundingSphere.radius / Math.sin(Math.PI / 4);

        // 相机当前的位置
        let currentX = this.#scene.camera.position.x;
        let currentY = this.#scene.camera.position.y;
        let currentZ = this.#scene.camera.position.z;

        // 旋转动画时间与旋转角度的比例尺
        let scaleX = d3.scaleLinear().domain([0, duration_changePerspective]).clamp(true);
        let scaleY = d3.scaleLinear().domain([0, duration_changePerspective]).clamp(true);
        let scaleZ = d3.scaleLinear().domain([0, duration_changePerspective]).clamp(true);

        // three.js 坐标系为右手坐标系
        switch (orientation) {
            //最简单时为绕x轴旋转，即x轴一直为0，y,x两个轴组成一个二维坐标系，以外界多边形中心的为圆心的，以外界多边形外接圆的半径为半径组成的圆。
            case Viewpoint.#PERSPECTIVE_BOTTOM:{
                scaleX.domain([0, duration_changePerspective]).range([currentX,0]);
                scaleY.domain([0, duration_changePerspective]).range([currentY,-(center.y + deltaHeight)]);

                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let y = scaleY(elapsed);
                    let x = this.#getX(center.y, y);
                    this.#scene.camera.position.setX(scaleX(elapsed));
                    this.#scene.camera.position.setY(y);
                    this.#scene.camera.position.setZ(x);
                    this.#scene.camera.lookAt(center);
                    //this.#scene.camera.lookAt(0,0,0);
                });
                break;
            }
            case Viewpoint.#PERSPECTIVE_TOP:{
                scaleX.domain([0, duration_changePerspective]).range([currentX,0]);
                scaleY.domain([0, duration_changePerspective]).range([currentY,center.y + deltaHeight]);

                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let y = scaleY(elapsed);
                    let x = this.#getX(center.y, y);
                    this.#scene.camera.position.setX(scaleX(elapsed));
                    this.#scene.camera.position.setY(y);
                    this.#scene.camera.position.setZ(x);

                    this.#scene.camera.lookAt(center);
                });
                break;
            }
            case Viewpoint.#PERSPECTIVE_FRONT:{
                scaleY.domain([0, duration_changePerspective]).range([currentY,0]);
                scaleZ.domain([0, duration_changePerspective]).range([currentZ,center.z + deltaHeight]);

                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let z = scaleZ(elapsed);
                    let x = this.#getX(center.z,z);
                    this.#scene.camera.position.setX(x);
                    this.#scene.camera.position.setY(scaleY(elapsed));
                    this.#scene.camera.position.setZ(z);


                    this.#scene.camera.lookAt(center);
                });
                break;
            }
            case Viewpoint.#PERSPECTIVE_AFTER:{
                scaleY.domain([0, duration_changePerspective]).range([currentY,0]);
                scaleZ.domain([0, duration_changePerspective]).range([currentZ,-(center.z + deltaHeight)]);

                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let z = scaleZ(elapsed);
                    let x = this.#getX(center.z,z);
                    this.#scene.camera.position.setX(x);
                    this.#scene.camera.position.setY(scaleY(elapsed));
                    this.#scene.camera.position.setZ(z);


                    this.#scene.camera.lookAt(center);
                });
                break;
            }
            case Viewpoint.#PERSPECTIVE_RIGHT:{
                scaleY.domain([0, duration_changePerspective]).range([currentY,0]);
                if (Math.abs(currentZ) < 0.0005) {
                    scaleZ.domain([0, duration_changePerspective]).range([0,currentZ]);
                }else {
                    scaleZ.domain([0, duration_changePerspective]).range([currentZ,0]);
                }

                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let z = scaleZ(elapsed);
                    let x = this.#getX(center.z,z);
                    this.#scene.camera.position.setX(x);
                    this.#scene.camera.position.setY(scaleY(elapsed));
                    this.#scene.camera.position.setZ(z);


                    this.#scene.camera.lookAt(center);
                });
                break;
            }
            case Viewpoint.#PERSPECTIVE_LEFT:{
                if (Math.abs(currentZ) < 0.0005) {
                    scaleZ.domain([0, duration_changePerspective]).range([0,currentZ]);
                }else {
                    scaleZ.domain([0, duration_changePerspective]).range([currentZ,0]);
                }
                scaleY.domain([0, duration_changePerspective]).range([currentY,0]);


                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let z = scaleZ(elapsed);
                    let x = this.#getX(center.z,z);
                    this.#scene.camera.position.setX(-x);
                    this.#scene.camera.position.setY(scaleY(elapsed));
                    this.#scene.camera.position.setZ(z);

                    this.#scene.camera.lookAt(center);
                });
                break;
            }
            default:{
                scaleX.domain([0, duration_changePerspective]).range([currentX,(center.z + deltaHeight)]);
                scaleY.domain([0, duration_changePerspective]).range([currentY,(center.y + deltaHeight)]);
                let timer = d3.timer((elapsed) => {
                    if (elapsed >= duration_changePerspective) {
                        timer.stop();
                    }
                    let y = scaleY(elapsed);
                    let x = this.#getX(center.y, y);
                    this.#scene.camera.position.setX(scaleX(elapsed));
                    this.#scene.camera.position.setY(y);
                    this.#scene.camera.position.setZ(x);

                    this.#scene.camera.lookAt(center);
                });
                break;
            }
        }

        this.#scene.camera.lookAt(center);
    }


    /**
     * 根据制定圆心，计算半圆上
     * @param cy 圆心的y坐标
     * @param y 点的y坐标
     * @returns {number} 该点的x长度
     */
    #getX = (cy,y) => {
        let r = this.#scene.boundingBox.getBoundingSphere().radius;

        if (Math.abs(cy - y) > r) {
            return 0;
        }

        return Math.sqrt(Math.pow(r,2) - Math.pow(Math.abs(cy - y),2));
    }

    /**
     * 视点飞到指定的地方
     * @param camera
     * @param options
     */
    flyTo(camera, options=defaultFlyToOpts){
        let duration = options.duration;
        // 相机当前的位置
        let currentPositionX = this.#scene.camera.position.x;
        let currentPositionY = this.#scene.camera.position.y;
        let currentPositionZ = this.#scene.camera.position.z;

        // 相机当前的角度
        let currentRotationX = this.#scene.camera.rotation.x,
            currentRotationY = this.#scene.camera.rotation.y,
            currentRotationZ = this.#scene.camera.rotation.z;

        // 旋转动画时间与旋转角度的比例尺
        let scalePositionX = d3.scaleLinear().domain([0, duration]).range([currentPositionX, camera.position.x]).clamp(true);
        let scalePositionY = d3.scaleLinear().domain([0, duration]).range([currentPositionY, camera.position.y]).clamp(true);
        let scalePositionZ = d3.scaleLinear().domain([0, duration]).range([currentPositionZ, camera.position.z]).clamp(true);

        let scaleRotationX = d3.scaleLinear().domain([0, duration]).range([currentRotationX, camera.rotation.x]).clamp(true),
            scaleRotationY = d3.scaleLinear().domain([0, duration]).range([currentRotationY, camera.rotation.y]).clamp(true),
            scaleRotationZ = d3.scaleLinear().domain([0, duration]).range([currentRotationZ, camera.rotation.z]).clamp(true);


        let timer = d3.timer(elapsed => {
            if (elapsed >= duration) {
                timer.stop();
            }

            let positionX = scalePositionX(elapsed),
                positionY = scalePositionY(elapsed),
                positionZ = scalePositionZ(elapsed),

                rotationX = scaleRotationX(elapsed),
                rotationY = scaleRotationY(elapsed),
                rotationZ = scaleRotationZ(elapsed);

            this.#scene.camera.position.set(positionX, positionY, positionZ);
            this.#scene.camera.rotation.set(rotationX, rotationY, rotationZ);
        });

    }


}
