let innerRoomNames = data.map(d=>d.innerRoomName).filter(d=>!!d);

console.log('cube', cube, 'innerRoomNames', innerRoomNames);


let div = document.createElement('div');
div.className = 'roomList';
div.style.position='absolute';
div.style.left = '10px';
div.style.top = '50px';

div.innerHTML = `<p>Room List:</p>`;

data.forEach((d, i)=>{
    div.innerHTML += `<div data-id="${i}">${d.name}</div>`
});
document.body.append(div);

let info = d3.select(document.body).append('div')
    .attr('id','info');

let currentIdx = null;

let opacityMeshs = [];
let hidMeshs = [];

d3.selectAll('.roomList div')
    .on('click',function () {
        let dom = d3.select(this);
        let idx =  +dom.attr('data-id');
        if (idx === 0) {
            currentIdx = null;
            d3.selectAll('.roomList div').style('background', 'none');
            scene.lookFrom("init");

            // opacityMeshs.forEach(m=>m.visible = true);
            [...opacityMeshs, ...hidMeshs].forEach(m=>{
                m.material.transparent = false;
                m.material.opacity = 1
            });

            hidMeshs=[];
            opacityMeshs = [];

            info.transition().duration(1000).style('top','-1000px');
            return;
        }
        currentIdx = idx;
        let d = data[idx];
        d3.selectAll('.roomList div').style('background', 'none');
        dom.style('background', '#000');
        scene.flyTo(d.camera,{duration: 1000});

        [...opacityMeshs, ...hidMeshs].forEach(m=>{
            m.material.transparent = false;
            m.material.opacity = 1
        });

        hidMeshs=[];
        opacityMeshs = [];

        let hidMesh = scene.models.getObjectByName(d.innerRoomName);
        hidMeshs.push(...hidMesh);
        hidMesh.forEach(m=>{
            m.material.transparent = true;
            m.material.opacity = 0.5
        });

        let opacityMesh = scene.models.getObjectByName(d.outerRoomName);
        opacityMeshs.push(...opacityMesh);
        //opacityMeshs.forEach(m=>m.visible = false);
        opacityMesh.forEach(m=>{
            m.material.transparent = true;
            m.material.opacity = 0.2
        });


        info.html("");

        let keys = Object.keys(d).filter(k => (k!== 'name' && k!=='camera' && k!=='innerRoomName' && k!=='outerRoomName' && k!=='cube'));
        keys.forEach(key=>{
            let div = info.append('div');
            div.append('p').html(key+':');
            div.append('iframe')
                .attr('width','500')
                .attr('height', '300')
                .attr('src', d[key])
        });
        info.transition().duration(1000).style('top','50px');
    })
    .on('mouseover', function () {
        d3.selectAll('.roomList div').style('background', 'none');
        d3.select(this).style('background', '#000');
        d3.select(`.roomList div[data-id="${currentIdx}"]`).style('background', '#000');
    })
    .on('mouseout', function () {
        d3.selectAll('.roomList div').style('background', 'none');
        d3.select(`.roomList div[data-id="${currentIdx}"]`).style('background', '#000');
    });
